// Style
import './assets/scss/main.scss';

// Jquery
window.$ = require('jquery');

// Greensock
import { TimelineLite, Power1, SteppedEase, Power4, Bounce, Back, Elastic, TweenMax, TimelineMax } from 'gsap';

// Scroll magic
import ScrollMagic from 'scrollmagic';
// require('scrollmagic/scrollmagic/uncompressed/plugins/debug.addIndicators');

// Fullpage
import fullpage from 'fullpage.js';

function isMobile() {
    return $('.is-mobile').css('display') == 'none';
}

$(document).ready(function() {
    // Init controller scroll magic
    const controller = new ScrollMagic.Controller();

    // Init fullpage
    new fullpage('#fullpage', {
        autoScrolling:true,
        scrollHorizontally: true,
        licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        fitToSection: true,
        controlArrows: false,
        anchors: ['home', 'about', 'projects', 'skills', 'contact'],
        menu: '.list-nav',
        afterRender: function() {
            fullpage_api.moveTo('home');
        },
        css3: true,
        onSlideLeave: function(section, origin, dest) {
            console.log({dest, origin});
            loadProjectsScene();
            changeActivePageItem.bind(pageItems[dest.index])();
        }
    });

    /**
     * Home Scene
     */
    const circleColor = $('.home .object.color');
    const circleWhite = $('.home .object.white');
    const circleBorder = $('.home .object.border');
    const circles = $('.home .object.circles');
    const wavy = $('.home .object.wavy');
    const center = $('#svg-center');
    const job = $('#job');
    const scrollDown = $('.scroll-down');

    function loadHomeScene() {
        const tl = new TimelineMax();
        tl.staggerTo(circleColor, 0.2, {css: {opacity: 1}, delay: 1}, 0);
        tl.staggerFrom(circleColor, 2, {css: {top: '-15%'}, ease: Elastic.easeOut}, 0, '-=0.2');
        tl.staggerTo(circleWhite, 0.2, {css: {opacity: 1}}, 0, '-=2');
        tl.staggerFrom(circleWhite, 2, {css: {top: '-15%'}, ease: Elastic.easeOut}, 0, '-=2.2');
        tl.staggerTo(circleBorder, 0.2, {css: {opacity: 1}}, 0, '-=1.9');
        tl.staggerFrom(circleBorder, 2, {css: {top: '-15%'}, ease: Elastic.easeOut}, 0, '-=2.1');
        tl.staggerTo(circles, 0.2, {css: {opacity: 1}}, 0, '-=2.3');
        tl.staggerTo(wavy, 0.2, {css: {opacity: 1}}, 0, '-=2');
        tl.to(job, 0.2, {css: {opacity: 1}}, '-=2');
        tl.to(scrollDown, 1, {css: {bottom: '10%'}}, '-=2');
        return tl;
    }

    function scrollHomeScene(progress) {
        const tl = new TimelineMax();
        tl.to(center, 0.4, {yPercent: progress * 10, ease: Power1.easeOut});
        tl.staggerTo(circleWhite, 0.4, {yPercent: progress * 100, ease: Power1.easeOut}, 0, '-=0.7');
        tl.staggerTo(circleColor, 0.4, {yPercent: progress * 100, ease: Power1.easeOut}, 0, '-=0.1');
        return tl;
    }

    const homeScene = new ScrollMagic.Scene({
        triggerElement: '.home',
        duration: '100%'
    })
    .addTo(controller);
    homeScene.on('start', function() {
        loadHomeScene();
    });
    homeScene.on('progress', function(event) {
        scrollHomeScene(event.progress);
    })

    /**
     * About scene
     */
    const textAbout = $('.about .text');
    const triangleColor = $('.about .object.color');
    const triangleWhite = $('.about .object.white');
    const triangleBorder = $('.about .object.border');
    const lines = $('.about .object.lines');
    let isFirstTimeEnterPortfolio = true;

    function loadAboutScene() {
        const tl = new TimelineMax();
        tl.staggerTo(triangleColor, 0.2, {css: {opacity: 1}, delay: 0.4}, 0);
        tl.staggerFrom(triangleColor, 2, {css: {top: '-15%'}, ease: Elastic.easeOut}, 0, '-=0.2');
        tl.staggerTo(triangleWhite, 0.2, {css: {opacity: 1}}, 0, '-=2');
        tl.staggerFrom(triangleWhite, 2, {css: {top: '-15%'}, ease: Elastic.easeOut}, 0, '-=2.2');
        tl.staggerTo(triangleBorder, 0.2, {css: {opacity: 1}}, 0, '-=1.9');
        tl.staggerFrom(triangleBorder, 2, {css: {top: '-15%'}, ease: Elastic.easeOut}, 0, '-=2.1');
        tl.staggerTo(lines, 0.2, {css: {opacity: 1}}, 0, '-=2.3');
        tl.to(textAbout, 0.5, {css: {top: '52%'}}, '-=2.2');
        return tl;
    }

    function scrollAboutScene(progress) {
        const tl = new TimelineMax();
        tl.staggerTo(triangleWhite, 0.4, {yPercent: progress * 100, ease: Power1.easeOut}, 0);
        tl.staggerTo(triangleColor, 0.4, {yPercent: progress * 100, ease: Power1.easeOut}, 0, '-=0.1');
        return tl;
    }

    const aboutScene = new ScrollMagic.Scene({
        triggerElement: '.about',
        duration: '100%',
    })
    .addTo(controller);
    aboutScene.on('start', function() {
        if (isFirstTimeEnterPortfolio) {
            loadAboutScene();
            isFirstTimeEnterPortfolio = false;
        }
    })
    aboutScene.on('progress', function(event) {
        scrollAboutScene(event.progress)
    })

    /**
     * Projects scene
     */
    const rectangleColor = $('.projects .object.color');
    const rectangleWhite = $('.projects .object.white');
    const rectangleBorder = $('.projects .object.border');
    let isFirstTimeEnterProjects = true;

    function loadProjectsScene() {

        const tl = new TimelineMax();
        tl.staggerTo(rectangleColor, 0.2, {css: {opacity: 1}, delay: 0}, 0);
        tl.staggerFrom(rectangleColor, 1, {css: {left: '150%'}, ease: Power1.easeOut, immediateRender: false}, 0, '-=0.2');
        tl.staggerTo(rectangleWhite, 0.2, {css: {opacity: 1}}, 0, '-=1');
        tl.staggerFrom(rectangleWhite, 1, {css: {left: '150%'}, ease: Power1.easeOut, immediateRender: false}, 0, '-=1.2');
        tl.staggerTo(rectangleBorder, 0.2, {css: {opacity: 1}}, 0, '-=1');
        tl.staggerFrom(rectangleBorder, 1, {css: {left: '150%'}, ease: Power1.easeOut, immediateRender: false}, 0, '-=1.1');

        return tl;
    }

    function scrollProjectsScene(progress) {
        const tl = new TimelineMax();
        tl.staggerTo(rectangleWhite, 0.4, {xPercent: progress * 50, ease: Power1.easeOut}, 0);
        tl.staggerTo(rectangleColor, 0.4, {xPercent: progress * 50, ease: Power1.easeOut}, 0, '-=0.1');
        return tl;
    }

    function showPagination() {
        if (isMobile()) {
            return TweenMax.to('.pagination', 0.4, {css: {bottom: '6%'}, ease: Power1.easeOut});
        } else {
            return TweenMax.to('.pagination', 0.4, {css: {bottom: '2%'}, ease: Power1.easeOut});
        }
    }

    function hidePagination() {
        return TweenMax.to('.pagination', 0, {css: {bottom: '-6%'}, ease: Power1.easeIn});
    }

    const projectsScene = new ScrollMagic.Scene({
        triggerElement: '.projects',
        duration: '100%',
    })
    .addTo(controller);
    projectsScene.on('start', function() {
        if (isFirstTimeEnterProjects) {
            loadProjectsScene();
            isFirstTimeEnterProjects = false;
        }
    })
    projectsScene.on('enter', function() {
        showPagination();
    })
    projectsScene.on('leave', function() {
        hidePagination();
    })
    projectsScene.on('progress', function(event) {
        scrollProjectsScene(event.progress)
    })

    /**
     * Skills scene
     */
    const skillLevel = $('.skill-level');
    const skillBoxes = $('.skill-box');
    const squareColor = $('.skills .object.color');
    const squareWhite = $('.skills .object.white');
    const squareBorder = $('.skills .object.border');
    const dots = $('.skills .dots');
    let isFirstTimeEnterSkills = true;

    function loadSkillsScene() {
        const tl = new TimelineMax();
        tl.staggerTo(squareColor, 0.2, {css: {opacity: 1}, delay: 0.5}, 0);
        tl.staggerFrom(squareColor, 2, {css: {top: '-15%'}, ease: Elastic.easeOut}, 0, '-=0.2');
        tl.staggerTo(squareWhite, 0.2, {css: {opacity: 1}}, 0, '-=2');
        tl.staggerFrom(squareWhite, 2, {css: {top: '-15%'}, ease: Elastic.easeOut}, 0, '-=2.2');
        tl.staggerTo(squareBorder, 0.2, {css: {opacity: 1}}, 0, '-=1.9');
        tl.staggerFrom(squareBorder, 2, {css: {top: '-15%'}, ease: Elastic.easeOut}, 0, '-=2.1');
        tl.staggerFrom(dots, 1, {css: {top: '100%'}, ease: Power1.easeOut}, 0, '-=2.2');
        tl.staggerTo(dots, 0.2, {css: {opacity: 1}}, 0, '-=2.3');
        tl.staggerFromTo(skillBoxes, 0.4, {autoAlpha: 0, y: 40}, {autoAlpha: 1, y: 0}, 0.1, '-=2.3');
        tl.staggerFrom(skillLevel, 0.4, {css: {width: '0%'}}, 0.1, '-=2.3');
        return tl;
    }

    function scrollSkillsScene(progress) {
        const tl = new TimelineMax();
        tl.staggerTo(squareWhite, 0.4, {yPercent: progress * 100, ease: Power1.easeOut}, 0);
        tl.staggerTo(squareColor, 0.4, {yPercent: progress * 100, ease: Power1.easeOut}, 0, '-=0.1');
        return tl;
    }

    const skillsScene = new ScrollMagic.Scene({
        triggerElement: '.skills',
        duration: '100%',
    })
    .addTo(controller);

    skillsScene.on('progress', function(event) {
        scrollSkillsScene(event.progress)
    })
    skillsScene.on('enter', function() {
        if (isFirstTimeEnterSkills) {
            loadSkillsScene();
            isFirstTimeEnterSkills = false;
        }
    })

    /**
     * Contact scene
     */
    const ovalColor = $('.contact .object.color');
    const ovalWhite = $('.contact .object.white');
    const ovalBorder = $('.contact .object.border');
    const triangleDots = $('.contact .object.dots');
    const itemSocial = $('.social .item');
    let isFirstTimeEnterContact = true;

    function loadContactScene() {
        const tl = new TimelineMax();
        tl.staggerTo(ovalColor, 0.2, {css: {opacity: 1}, delay: 0.5}, 0);
        tl.staggerFrom(ovalColor, 1, {css: {left: '150%'}, ease: Power1.easeOut}, 0, '-=0.2');
        tl.staggerTo(ovalWhite, 0.2, {css: {opacity: 1}}, 0, '-=1');
        tl.staggerFrom(ovalWhite, 1, {css: {left: '150%'}, ease: Power1.easeOut}, 0, '-=1');
        tl.staggerTo(ovalBorder, 0.2, {css: {opacity: 1}}, 0, '-=1.1');
        tl.staggerFrom(ovalBorder, 1, {css: {left: '150%'}, ease: Power1.easeOut}, 0, '-=1.2');
        tl.staggerTo(triangleDots, 0.2, {autoAlpha: 1}, 0, '-=1');
        tl.staggerFromTo(itemSocial, 0.4, {autoAlpha: 0, x: 40}, {autoAlpha: 1, x: 0}, 0.1, '-=1');
        return tl;
    }

    function scrollContactScene(progress) {
        const tl = new TimelineMax();
        tl.staggerTo(ovalWhite, 0.4, {xPercent: progress * 80, ease: Power1.easeOut}, 0);
        tl.staggerTo(ovalColor, 0.4, {xPercent: progress * 80, ease: Power1.easeOut}, 0, '-=0.1');
        return tl;
    }

    const contactScene = new ScrollMagic.Scene({
        triggerElement: '.contact',
        duration: '100%',
    })
    .addTo(controller);
    contactScene.on('progress', function(event) {
        scrollContactScene(event.progress)
    })
    contactScene.on('enter', function() {
        if (isFirstTimeEnterContact) {
            loadContactScene();
            isFirstTimeEnterContact = false;
        }
    })

    /**
     * Navigation
     */
    const navigation = $('#navigation');
    const navItems = $('#navigation ul li');
    const buttonCloseNav = $('#navigation .button');
    const buttonOpenNav = $('.hamburger-button');

    function openNavigation() {
        let tl = new TimelineMax();
        tl.to(navigation, 0.5, { css: { width: '300px'}, ease: Power1.easeOut })
        tl.staggerFromTo(navItems, 0.3, {x: 30, autoAlpha: 0}, {x: 0, autoAlpha: 1}, 0.1, '-=0.3')
        return tl;
    }

    function closeNavigation() {
        let tl = new TimelineMax();
        tl.staggerTo(navItems, 0.3, {x: 30, autoAlpha: 0}, 0.2)
        tl.to(navigation, 0.4, { css: { width: '0px'}, ease: Power1.easeIn }, '-=0.5');
        return tl;
    }

    buttonOpenNav.click(function() {
        openNavigation();
    });

    buttonCloseNav.click(function() {
        closeNavigation();
    });

    navItems.click(function() {
        $('#navigation ul li a').removeClass('active');
        $(this).children('a').addClass('active');
    })

    /**
     * Pagination
    */
    function changeActivePageItem() {
        pageItems.removeClass('active');
        $(this).addClass('active');
    }
    const pageItems = $('.pagination .dot');
    pageItems.click(function() {
        changeActivePageItem.bind(this)();
    })
});
